.mediaContainer {
    position: relative;
    display: inline-block;
    margin: auto;
    width: 300px;
    border: 1px solid;
  }
  
  .mediaOverlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(180, 180, 180, 0.6);
  }
  
  #media-input {
    display: block;
    width: 200px;
    height: 200px;
    line-height: 200px;
    opacity: 0;
    position: relative;
    z-index: 9;
  }
  
  .media-icon {
    display: block;
    color: #ffffff;
    font-size: 2em;
    height: 200px;
    line-height: 200px;
    position: absolute;
    z-index: 0;
    width: 100%;
    text-align: center;
    top: 0;
  }
  
  .imgObject {
    width: 200px;
    height: 200px;
    display: block;
    border: 1px solid silver;
  }
  
  .media-control {
    margin-top: 30px;
  }
  
  .editProfile {}
  
  .saveProfile {}
  
  /* button */
  .btn {
    margin: 10px 3px;
    display: inline-block;
    height: 38px;
    padding: 0 30px;
    color: #555;
    text-align: center;
    font-size: 11px;
    font-weight: 600;
    line-height: 38px;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid #bbb;
    cursor: pointer;
    box-sizing: border-box;
  }
  
  .btn>i {
    color: #555;
    font-size: 12px !important;
    margin-right: 10px;
  
  }
  
  .EditHotelDetails {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    width: 95%;
    margin: auto;
    margin-bottom: 40px;
  }
  
  .inputGroup {
    background-color: #fff;
    display: block;
    margin: 10px 0;
    position: relative;
    width: 200px;
  }
  
  .inputGroup label {
    padding: 12px 30px;
    width: 85%;
    display: block;
    text-align: left;
    color: #3C454C;
    cursor: pointer;
    position: relative;
    z-index: 2;
    transition: color 200ms ease-in;
    overflow: hidden;
  }
  
  .inputGroup label:before {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    content: '';
    background-color: #006FBF;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale3d(1, 1, 1);
    transition: all 300ms cubic-bezier(0.4, 0.0, 0.2, 1);
    opacity: 0;
    z-index: -1;
  }
  
  .inputGroup label:after {
    width: 32px;
    height: 32px;
    content: '';
    border: 2px solid #D1D7DC;
    background-color: #fff;
    background-image: url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");
    background-repeat: no-repeat;
    background-position: 2px 3px;
    border-radius: 50%;
    z-index: 2;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    transition: all 200ms ease-in;
  }
  
  
  .inputGroup  input:checked~label {
    color: #fff;
  }
  
  .inputGroup  input:checked~label:before {
    transform: translate(-50%, -50%) scale3d(56, 56, 1);
    opacity: 1;
  }
  
  .inputGroup  input:checked~label:after {
    background-color: #54E0C7;
    border-color: #54E0C7;
  }
  
  
  .inputGroup  input {
    width: 32px;
    height: 32px;
    order: 1;
    z-index: 2;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    visibility: hidden;
  }
  
  
  
  .Form {
    padding: 0 16px;
    max-width: 550px;
    margin: 50px auto;
    font-size: 18px;
    font-weight: 600;
    line-height: 36px;
  }
  
  .name  input, select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  h5{
    font-size: 18px;
    font-weight: 600;
    line-height: 36px;
  }
  .submitButton {
    width:50%;
    margin: auto;
  }
  .submitButton > button{
    width:100%;
    text-align: center;
    color: white;
    height: 42px;
    font-weight: 600;
    font-size: 20px;
    cursor: pointer;
    border-radius: 4px;
    border: 1px solid white;
    background-color: #0eb743;
    margin: auto;
    margin-top: 20px;
  }
 .header{
    color: #006FBF;
    text-transform: uppercase;
    align-items: center;
    margin: auto;
    text-align: center;
    font-size: 25px;
 }
 .Form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: auto;
    width: 100%;
 }
 h5{
    background-color: #d3dce32d;
    width: 200px;
    padding: 10px;
 }
